const initialValue = {}

const discrepancyReducer = (state = initialValue, action) => {
  switch (action.type) {
    case 'INIT_CLAIM_RECORDS':
      return initialValue

    case 'SET_CLAIM_RECORDS':
      return { ...initialValue, ...state, ...action.payload }

    default:
      return state
  }
}

export default discrepancyReducer
