import instance from './instance'
const axios = instance('/icd')

export default class IcdServices {
  static count () {
    return axios({
      url: '/api/icd10-codes/count',
      method: 'GET'
    })
  }

  // static getICD (params) {
  //   return axios({
  //     url: `/api/icd10-codes`,
  //     method: 'GET',
  //     params
  //   })
  // }

  static getList (offset, limit, skip) {
    return axios({
      // URL approach is based on swagger generated URL
      // based on observation on manual testing on the resulting URL, this approach seems to 
      // be processed faster than the first approach (passing JSON object with 'fields' keys)
      url: `/api/icd10-codes?filter[offset]=${offset}&filter[limit]=${limit}&filter[skip]=${skip}`,
      method: 'GET'
    })
  }
}
