import { IndexedDbHelper } from 'Utilities/IndexedDbHelper'
import instance from '../Api/instance'
// const axios = instance('/c/ce')
// const FlexSearch = require('flexsearch');

export default class IcdCptStorageServices {
  /**
   * Initializes a IndexedDb database and the object stores needed to store/access ICDs and CPTs
   *
   * @param {function} callback function to be invoked upon completion
   */
  static initializeStorage (callback) {
    // IcdCptStorageServices.flexSearchIndex = {}

    try {
      let dbName = IcdCptStorageServices.DB_NAME

      IndexedDbHelper.deleteDb(dbName, function () {
        IndexedDbHelper.openDb(
          dbName,

          function (db, event) {
            IndexedDbHelper.createStoreOnDb(db, IcdCptStorageServices.OBJECT_STORE_NAME_CPT, 'code')
            IndexedDbHelper.createStoreOnDb(db, IcdCptStorageServices.OBJECT_STORE_NAME_ICD, 'full_code')
          },

          function (db, event) {
            console.log('success opening ' + dbName)
            IcdCptStorageServices.db = db

            if (callback) callback()
          },

          function (event) {
            console.error('error opening ' + dbName)
            console.error(event)
            if (callback) callback()
          }
        )
      })
    } catch (e) {
      console.error('entering initializeStorage catch block')
      console.error(e)
      console.error('leaving initializeStorage catch block')
    }
  }

  /**
   * Saves ICD entries
   *
   * @param {array} data entries to be saved
   * @param {function} callback function to be invoked upon success or end failure
   */
  static saveIcdEntries (data, onSaveComplete, onLoopEnd) {
    try {
      IcdCptStorageServices.records[IcdCptStorageServices.OBJECT_STORE_NAME_ICD] = data

      IndexedDbHelper.openDbAndSave(
        IcdCptStorageServices.DB_NAME,
        IcdCptStorageServices.OBJECT_STORE_NAME_ICD,
        data,
        onSaveComplete,
        onLoopEnd
      )
    } catch (e) {
      console.error('entering saveIcdEntries catch block')
      console.error(e)
      console.error('leaving saveIcdEntries catch block')
    }
  }

  /**
   * Saves CPT entries
   *
   * @param {array} data entries to be saved
   * @param {function} callback function to be invoked upon success or end failure
   */
  static saveCptEntries (data, onSaveComplete, onLoopEnd) {
    try {
      IcdCptStorageServices.records[IcdCptStorageServices.OBJECT_STORE_NAME_CPT] = data

      IndexedDbHelper.openDbAndSave(
        IcdCptStorageServices.DB_NAME,
        IcdCptStorageServices.OBJECT_STORE_NAME_CPT,
        data,
        onSaveComplete,
        onLoopEnd
      )
    } catch (e) {
      console.error('entering saveCptEntries catch block')
      console.error(e)
      console.error('leaving saveCptEntries catch block')
    }
  }

  static readStorageEntries (storeName, callback) {
    try {
      // Load entries from IndexedDB and into memory records
      IndexedDbHelper.openDb(
        IcdCptStorageServices.DB_NAME,
        null,
        function (db, event) {
          IndexedDbHelper.getAll(db, storeName, function (entries) {
            callback(entries)
          })
        },
        null
      )
    } catch (e) {
      console.error('entering readStorageEntries catch block')
      console.error(e)
      console.error('leaving readStorageEntries catch block')
    }
  }

  static loadEntries (storeName, callback) {
    try {
      // If memory records is still empty
      if (!IcdCptStorageServices.records[storeName]) {
        IcdCptStorageServices.readStorageEntries(storeName, function (entries) {
          IcdCptStorageServices.records[storeName] = entries
          callback(IcdCptStorageServices.records[storeName])
        })
      } else {
        // Use entries from memory records
        callback(IcdCptStorageServices.records[storeName])
      }
    } catch (e) {
      console.error('entering loadEntries catch block')
      console.error(e)
      console.error('leaving loadEntries catch block')
    }
  }

  /**
   * Peform lookups.
   *
   * This method has been created to emulate the output of ClaimsEncodingServices.lookups() so that
   * autosuggest features that rely on the behavior ClaimsEncodingServices.lookups() will still function
   * if this method is used as a replacement.
   *
   * @see ClaimsEncodingServices.lookups()
   */
  static lookups ({ on, params }) {
    try {
      let promise = new Promise(function (resolve, reject) {
        let storeName = null
        if (on === 'icd-10') {
          storeName = IcdCptStorageServices.OBJECT_STORE_NAME_ICD
        } else if (on === 'cpt') {
          storeName = IcdCptStorageServices.OBJECT_STORE_NAME_CPT
        }

        IcdCptStorageServices.loadEntries(storeName, function (entries) {
          // Perform search
          let value = params.query.toLowerCase()

          let result = []
          let filterKey = ''
          if (on === 'icd-10') {
            filterKey = 'full_description'
            switch (params.by) {
              case 'code':
                filterKey = 'full_code'
                break
              case 'category':
                filterKey = 'category_code'
                break
            }

            result = entries.filter(entry => ('' + entry[filterKey]).toLowerCase().includes(value))

            if (result && result.length > 0) {
              // Construct output with data structure similar to the output of ClaimsEncodingServices.lookups()
              result = result.map(entry => ({ description: entry.full_description, code: entry.full_code, ...entry }))
            }
          } else if (on === 'cpt') {
            filterKey = 'label'
            if (params.by === 'code') filterKey = 'code'
            result = entries.filter(entry => ('' + entry[filterKey]).toLowerCase().includes(value))

            if (result && result.length > 0) {
              // Construct output with data structure similar to the output of ClaimsEncodingServices.lookups()
              result = result.map(entry => ({ description: entry.label, code: entry.code, ...entry }))
            }
          }

          // Sort by filter key
          result.sort((a, b) => {
            let item1 = ('' + a[filterKey]).toLowerCase()
            let item2 = ('' + b[filterKey]).toLowerCase()

            if (item1 > item2) {
              return 1
            }

            return -1
          })

          // Pick top X entries
          result = result.slice(0, IcdCptStorageServices.LOOKUP_LIMIT)

          result = { data: { data: result } }

          resolve(result)
        })
      })

      return promise
    } catch (e) {
      console.error('entering lookups catch block')
      console.error(e)
      console.error('leaving lookups catch block')
    }

    // return axios({
    //   url: `/lookups/${on}`,
    //   method: 'GET',
    //   params: { limit: 10, ...params }
    // })
    // Expected: [{code: "A000", description: "Cholera due to Vibrio cholerae 01, biovar cholerae"}]
  }
}

IcdCptStorageServices.DB_NAME = 'claims'
IcdCptStorageServices.OBJECT_STORE_NAME_ICD = 'icds'
IcdCptStorageServices.OBJECT_STORE_NAME_CPT = 'cpts'

IcdCptStorageServices.LOOKUP_LIMIT = 10

IcdCptStorageServices.records = {}

