const initialValue = {
  diagnosis: {},
  procedure: {},
  hospital_item: {},
  professional_fee: {}
}

const errorReducer = (state = initialValue, action) => {
  switch (action.type) {
    case 'RESET':
      return initialValue

    case 'RESET_ERROR_BASE':
      return () => {
        return {
          diagnosis: { ...state.diagnosis },
          procedure: { ...state.procedure },
          hospital_item: { ...state.hospital_item },
          professional_fee: { ...state.professional_fee }
        }
      }

    case 'RESET_ERROR_DIAGNOSIS':
      return (() => {
        // const { index } = action.payload
        return {
          ...state,
          diagnosis: {}
        }
      })()

    case 'RESET_ERROR_PROCEDURE':
      return (() => {
        // const { index } = action.payload
        return {
          ...state,
          procedure: {}
        }
      })()

    case 'RESET_ERROR_HOSPITAL_ITEM':
      return (() => {
        // const { index } = action.payload
        return {
          ...state,
          hospital_item: {}
        }
      })()

    case 'RESET_ERROR_PROFESSIONAL_FEE':
      return (() => {
        // const { index } = action.payload
        return {
          ...state,
          professional_fee: {}
        }
      })()

    case 'SET_ERROR_BASE':
      return (() => {
        const { details } = action.payload
        return { ...state, ...details }
      })()

    case 'SET_ERROR_DIAGNOSIS':
      return (() => {
        const { index, details } = action.payload
        return {
          ...state,
          diagnosis: { ...state.diagnosis, [index]: { ...details } }
        }
      })()

    case 'SET_ERROR_PROCEDURE':
      return (() => {
        const { index, details } = action.payload
        return {
          ...state,
          procedure: { ...state.procedure, [index]: { ...details } }
        }
      })()

    case 'SET_ERROR_HOSPITAL_ITEM':
      return (() => {
        const { index, details } = action.payload
        return {
          ...state,
          hospital_item: { ...state.hospital_item, [index]: { ...details } }
        }
      })()

    case 'SET_ERROR_PROFESSIONAL_FEE':
      return (() => {
        const { index, details } = action.payload
        return {
          ...state,
          professional_fee: { ...state.professional_fee, [index]: { ...details } }
        }
      })()

    default:
      return state
  }
}

export default errorReducer
