import React from 'react'
import { Link } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import SearchTextComponent from 'SharedComponents/SearchTextComponent'

import * as moment from 'moment'

// Services
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import BatchEncodingServices from 'Services/Api/BatchEncodingServices'

const useStyles = makeStyles(theme => ({
  searchAndAddContainer: {
    padding: '0 0 10px 0'
  },
  addButton: {
    width: 200
  }
}))

const SearchAndAdd = ({ classes }) => {
  return (
    <Grid item container alignItems='center' wrap='nowrap' className={classes.searchAndAddContainer}>
      <Grid item xs={12}>
        <SearchTextComponent />
      </Grid>

      <Grid item container className={classes.addButton} justify='flex-end'>
        <Link to='/bundles/create'>
          <Button color='primary' size='medium'>
            + Add new bundle
          </Button>
        </Link>
      </Grid>
    </Grid>
  )
}

const BundleList = ({ bundle = [], rowClick }) => {
  return (
    <Paper className='table'>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Bundle No.</TableCell>
            <TableCell>SOA No.</TableCell>
            <TableCell>Medical Partner ID</TableCell>
            <TableCell>Facility Name</TableCell>
            <TableCell>Doctor's Name</TableCell>
            <TableCell>Total # of Batch</TableCell>
            <TableCell>Date Received</TableCell>
            <TableCell>Due Date</TableCell>
            <TableCell>Status </TableCell>
          </TableRow>
        </TableHead>

        <TableBody>
          {bundle.items.slice(0, 10).map(i => (
            <TableRow hover key={i.bundle_number} onClick={event => rowClick(i.bundle_number)}>
              <TableCell>{i.bundle_number}</TableCell>
              <TableCell>{i.soa_number}</TableCell>
              <TableCell>{i.facility_id}</TableCell>
              <TableCell>{i.facility_name}</TableCell>
              <TableCell>{i.doctor_name}</TableCell>
              <TableCell>{i.totalNumberOfBatch || 0}</TableCell>
              <TableCell>{moment(i.date_received).format('MM/DD/YYYY')}</TableCell>
              <TableCell>
                {moment(i.date_received)
                  .add(30, 'days')
                  .format('MM/DD/YYYY')}
              </TableCell>
              <TableCell>{i.status}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>

      {bundle.fetching ? (
        <Grid container className='table-loading'>
          <span>Fetching Data...</span>
        </Grid>
      ) : (
        ''
      )}

      {!bundle.fetching && !bundle.items.length ? (
        <Grid container className='table-no-data'>
          <span>No data</span>
        </Grid>
      ) : (
        ''
      )}
    </Paper>
  )
}

export default function BundlesPage (props) {
  PageTitleService('Dashboard')
  BreadcrumbsService.setSelectedItems(['dashboard'])

  const [bundle, setBundles] = React.useState({ fetching: true, items: [] })

  React.useEffect(() => {
    BatchEncodingServices.getBundles().then(res => {
      setBundles({ fetching: false, items: res.data.data })
    })
  }, [])
  const rowClick = bundle_number => {
    props.history.push(`/bundles/${bundle_number}`)
  }

  const classes = useStyles()
  return (
    <React.Fragment>
      <SearchAndAdd classes={classes} />
      <BundleList bundle={bundle} rowClick={rowClick} />
    </React.Fragment>
  )
}
