import instance from './instance'
const axios = instance()

export default class CommonServices {
  static login (data) {
    return axios({
      url: '/auth/login',
      method: 'POST',
      data: { ...data, isAuthorized: false }
    })
  }

  static getMe (data) {
    return axios({
      url: '/me',
      method: 'GET'
    })
  }
}
