import React from 'react'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import MenuItem from '@material-ui/core/MenuItem'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'

// CSS Classes
import classes from './DashboardTileComponent.module.scss'

/**
 * Create dashboard tile component
 *
 * @param children child components
 * @param count count value to be displayed; keys: color, value
 * @param header tile header label; keys: color, value
 * @param title title string
 * @param viewList view list
 * @param onFilterChange change filter handler
 * @param isFilterShown flag for showing filter
 */
const DashboardTileComponent = ({
  children,
  count,
  header,
  title,
  viewList,
  actionHeader = null,
  onFilterChange,
  isFilterShown = true,
  id,
  isTicketFilterShown,
  isTpaFilterShown
}) => {
  const [filter, setFilter] = React.useState('this-month')
  const [ticketFilter, setTicketFilter] = React.useState('assigned-to-me')
  const [tpaFilter, setTpaFilter] = React.useState('show-all')
  const handleFilterChange = event => {
    setFilter(event.target.value)
    if (typeof onFilterChange === 'function') onFilterChange(event.target.value)
  }
  const handleTicketFilterChange = event => {
    setTicketFilter(event.target.value)
  }
  const handleTpaFilterChange = event => {
    setTpaFilter(event.target.value)
  }

  return (
    <Paper className={classes.root} id={id}>
      <div className={classes.wrapper}>
        <div className={classes.header}>
          {header ? (
            <div className={classes.count}>
              <Typography variant='h5' className={`color-${header.color}`}>
                {header.value}
              </Typography>
            </div>
          ) : (
            ''
          )}
          <div className={classes['first-row']}>
            <div className={classes.count}>
              <Typography variant='h5' className={`color-${count.color}`} id={count.id}>
                {count.value || 0}
              </Typography>
            </div>
            {/* Filter for date */}
            {isFilterShown ? (
              <div className={classes.filter}>
                <span>
                  <FormControl>
                    <Select
                      className={classes['select']}
                      value={filter}
                      onChange={handleFilterChange}
                      id={`${id}-filter`}
                    >
                      <MenuItem value='today' id={`${id}-today`}>
                        <Typography className={classes.input}>Today</Typography>
                      </MenuItem>
                      <MenuItem value='this-week' id={`${id}-this-week`}>
                        <Typography className={classes.input}>This Week</Typography>
                      </MenuItem>
                      <MenuItem value='this-month' id={`${id}-this-month`} selected>
                        <Typography className={classes.input}>This Month</Typography>
                      </MenuItem>
                    </Select>
                  </FormControl>
                </span>
              </div>
            ) : (
              ''
            )}
            {/* Filter for TPA to be replenished */}
            {isTpaFilterShown ? (
              <div className={classes.filter}>
                <span>
                  <FormControl>
                    <Select
                      className={classes['select']}
                      value={tpaFilter}
                      onChange={handleTpaFilterChange}
                      id={`${id}-filter`}
                    >
                      <MenuItem value='below-threshold' id={`${id}-below-threshold`}>
                        <Typography className={classes.input}>Below Threshold</Typography>
                      </MenuItem>
                      <MenuItem value='show-all' id={`${id}-show-all`} selected>
                        <Typography className={classes.input}>Show All</Typography>
                      </MenuItem>
                    </Select>
                  </FormControl>
                </span>
              </div>
            ) : (
              ''
            )}
            {/* Filter for Ticket waiting for response */}
            {isTicketFilterShown ? (
              <div className={classes.filter}>
                <span>
                  <FormControl>
                    <Select
                      className={classes['select']}
                      value={ticketFilter}
                      onChange={handleTicketFilterChange}
                      id={`${id}-filter`}
                    >
                      <MenuItem value='assigned-to-me' id={`${id}-assigned-to-me`} selected>
                        <Typography className={classes.input}>Assigned to Me</Typography>
                      </MenuItem>
                      <MenuItem value='assigned-to-all' id={`${id}-assigned-to-all`}>
                        <Typography className={classes.input}>Assigned to All</Typography>
                      </MenuItem>
                    </Select>
                  </FormControl>
                </span>
              </div>
            ) : (
              ''
            )}
          </div>

          <div className={classes['second-row']}>
            <div className={classes.title}>
              <Typography variant='subtitle2'>{title}</Typography>
            </div>
            <div className={classes.action}>{actionHeader || ''}</div>
          </div>
        </div>

        <div className={classes.list}>
          {React.Children.map(children, child => {
            return child
          })}
        </div>

        <div className={classes.action}>{viewList || ''}</div>
      </div>
    </Paper>
  )
}

DashboardTileComponent.displayName = 'DashboardTileComponent'

export default DashboardTileComponent
