import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

const schema = Joi.object({
  code: Joi.string().trim()
    .label('ICD10 Code')
    .required(),
  description: Joi.string().trim()
    .label('ICD10 Description')
    .required()
}).options({ abortEarly: false })

const validate = data => {
  let errors = {}
  let hasPrimary = false
  data.map((item, index) => {
    if (item.primary) hasPrimary = true
    const { error } = ExtractValidationResult(schema.validate({ description: item.description, code: item.code }))
    if (error) errors = { ...errors, [index]: { index, error } }
  })

  if (!data.length) {
    errors = { ...errors, list: { index: 'list', error: 'Diagnosis should have at least one item' } }
  } else if (!hasPrimary) {
    errors = { ...errors, list: { index: 'list', error: 'Diagnosis should have at least one primary item' } }
  }

  return { ...errors }
}

export default validate
