import React from 'react'
import { Switch, Route } from 'react-router-dom'
import Grid from '@material-ui/core/Grid'
import CommonServices from 'Services/Api/CommonServices'
import { to } from 'await-to-js'
import { Provider } from 'react-redux'
import BillingIndexedDB from '../../Utilities/BillingIndexedDB'
import RbacController from '../../Controllers/RbacController'
import UserData from './SecurityConfig/data/billing-user-mock.json'

// Compoents
import HeaderComponent from 'SharedComponents/HeaderComponent/HeaderComponent'
import { SideBarComponent } from 'SharedComponents/SideBarComponent/SideBarComponent'
import { PageTitleComponent } from 'SharedComponents/PageTitleComponent/PageTitleComponent'
import { BreadcrumbsComponent } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingComponent } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Modules
import Claims from './Claims'
import Billing from './BillingAndCollection'

// Error Page
import { ErrorPageInit, ErrorPage } from 'SharedComponents/404/404'

// classes
import classes from './Protected.module.scss'

// Store
import Store from './Store'

// Services
import ProtectedPageScrollEvent from 'Services/ProtectedPageScrollEvent'

// Controllers
import { GeneralController } from 'Controllers/GeneralController'

export default function Protected ({ history }) {
  const [selectedModule, setSelectedModule] = React.useState('')
  const [isAauthorized, setIsAuthorized] = React.useState(false)
  const [containerNode, setContainerNode] = React.useState(null)

  const getMe = async () => {
    const [err, res] = await to(CommonServices.getMe())

    const stores = [
      { name: 'user_data', keyPath: ['key'] },
      { name: 'rbac_data', keyPath: ['key'] }
    ]

    if (err) {
      localStorage.removeItem('token')
      window.location.replace('#/login')
    } else {
      const { data } = res.data
      console.log(data.username)
      console.log(UserData)
      const userLogged = UserData.filter(user => user.login.username === data.username)
      console.log(userLogged)
      RbacController.extractPolicies({ rbac: userLogged }).then(res => {
        console.log(res)
        const extractedPolicies = res
        BillingIndexedDB.connect({ dbName: 'billingAndCollection', stores }).then(res => {
          BillingIndexedDB.update({ db: res, storeName: 'rbac_data', data: { policies: extractedPolicies } })
        })
      })
      // RbacController.setUserManagementData()
      // BillingIndexedDB.connect({ dbName: 'RbacDatabase', stores }).then(res => {
      //   console.log(res)
      //   BillingIndexedDB.update({ db: res, storeName: 'user_data', data: { test: 'data' } })
      // })

      localStorage.setItem('me', JSON.stringify(data))
      _setSelectedModule('BILLING')
      setIsAuthorized(true)
    }
  }

  const _setSelectedModule = m => {
    setSelectedModule(m)
  }

  React.useEffect(() => {
    console.log()
    const token = localStorage.getItem('token')
    if (!token) {
      window.location.replace('#/login')
      return
    }

    // Invoke initialization of ICD and CPT entries
    // This was previously done in LoginPage, but because there hims-app
    // will have it own login page, the operation have to be relocated elsewhere.
    //
    // TODO: transfer the call to this method to somewhere more appropriate
    GeneralController.initializeIcdAndCptEntries()

    getMe()
  }, [])

  const routesForModule = () => {
    switch (selectedModule) {
      // case 'CLAIMS':
      //   return <Route path='/' component={Claims} />
      case 'BILLING':
        return <Route path='/' component={Billing} />
      default:
        break
    }
  }

  const logout = () => {
    RbacController.clearRbacData()
    localStorage.removeItem('token')
    localStorage.removeItem('me')
    window.location.replace('#/login')
  }

  const scrollContainer = event => {
    const { scrollTop } = containerNode
    containerNode.scrollTo({ top: scrollTop + event.deltaY })
  }

  return (
    <React.Fragment>
      {isAauthorized && (
        <div className={classes['main-container']}>
          <Provider store={Store}>
            <SideBarComponent />
          </Provider>

          <div className={classes['sub-container']}>
            <HeaderComponent logout={logout} />
            <Grid item className={classes.content} onScroll={ProtectedPageScrollEvent.publish} ref={setContainerNode}>
              <Provider store={Store}>
                <PageTitleComponent />
                <BreadcrumbsComponent />
                <LoadingComponent onScrollContainer={scrollContainer} containerNode={containerNode} />
              </Provider>
              <Switch>{routesForModule()}</Switch>
            </Grid>
          </div>
        </div>
      )}
    </React.Fragment>
  )
}
