import BillingIndexedDB from 'Utilities/BillingIndexedDB'
import PolicyData from '../Pages/Protected/SecurityConfig/data/billing-policies.json'

const dbName = 'billingAndCollection'
const stores = [
  { name: 'user_data', keyPath: ['key'] },
  { name: 'rbac_data', keyPath: ['key'] }
]

export default class RbacController {
  static async getPolicyData () {
    const db = await BillingIndexedDB.connect({ dbName, stores })

    if (!db) return

    let result = await BillingIndexedDB.getData({ db, storeName: 'rbac_data' })
    console.log(result)

    if (!result.length) return

    let role_policies = {}

    for (let i of result) role_policies[i.key] = i.value

    return role_policies
  }

  static async setUserManagementData (data) {
    const db = await BillingIndexedDB.connect({ dbName, stores })

    if (!db) return

    await BillingIndexedDB.update({ db, storeName: 'user_data', data })
  }

  static async clearRbacData (data) {
    const db = await BillingIndexedDB.connect({ dbName, stores })
    if (!db) return
    console.log(db)

    await BillingIndexedDB.clear({ db, storeName: 'rbac_data' })
  }

  static async initializeBaseUrls () {
    const db = await BillingIndexedDB.connect({ dbName, stores })

    if (!db) return

    let result = await BillingIndexedDB.getData({ db, storeName: 'config' })

    if (!result.length) return

    let config = {}

    for (let i of result) config[i.key] = i.value

    if (config['PARTNER_URL']) {
      // window.localStorage.partnerUrl = config['PARTNER_URL']
    }

    if (config['CLIENT_URL']) {
      // window.localStorage.clientUrl = config['CLIENT_URL']
    }
  }

  static async extractPolicies ({ rbac }) {
    const rbacObject = rbac[0].login.rbac
    const policies = []
    for (let i = 0; i < rbacObject.length; i++) {
      let filteredPolicies = PolicyData.filter(policy => policy.policy_id === rbacObject[i].policy_id)
      if (filteredPolicies.length !== 0) {
        let features = PolicyData.filter(policy => policy.policy_id === rbacObject[i].policy_id)[0].features
        for (let i = 0; i < features.length; i++) {
          policies.push(features[i])
        }
      }
    }

    return policies
  }
}
