import Joi from '@hapi/joi'

// Services
import ExtractValidationResult from 'Services/ExtractValidationResult'

const schema = Joi.object({
  approval_code: Joi.string().trim()
    .label('Approval Code')
    .required(),
  loa_number: Joi.string().trim()
    .label('LOA No.')
    .required(),
  member_account_number: Joi.string().trim()
    .label('Member Account No.')
    .required(),
  member_last_name: Joi.string().trim()
    .label('Member Last Name')
    .required(),
  member_first_name: Joi.string().trim()
    .label('Member First Name')
    .required(),
  member_middle_name: Joi.string().trim()
    .label('Member Middle Name')
    .allow('', null),
  facility_id: Joi.string().trim()
    .label('Medical Partner ID')
    .required(),
  facility_name: Joi.string().trim()
    .label('Medical Partner (Facility) Name')
    .required(),
  date_from: Joi.date()
    .label('Admission Date (From)')
    .required()
    .max('now'),
  date_to: Joi.date()
    .label('Admission Date (To)')
    .required()
    .min(Joi.ref('date_from'))
    .messages({
      'date.min': `"Admission Date (From)" should be on or before "Admission Date (To)"`
    }),
  pro_id: Joi.string().trim()
    .label('PRO ID')
    .required(),
  pro_name: Joi.string().trim()
    .label('PRO Name')
    .required(),
  with_corporate_guarantee: Joi.string().trim()
    .label('Corporate Guaranty')
    .allow('', null),
  loa_notes: Joi.string().trim()
    .label('LOA Notes')
    .allow('', null)
}).options({ abortEarly: false })

const validate = data => ExtractValidationResult(schema.validate(data))

export default validate
