import IcdServices from 'Services/Api/IcdServices'
import CptServices from 'Services/Api/CptServices'
import IcdCptStorageServices from 'Services/Storage/IcdCptStorageServices'

/**
 * General controller prototype class intended to contain app-level controlloer
 * logic or any general controller logic that may not be appropriate to be contained
 * within a the source file of a visual or graphical component.
 */
function GeneralController () {}

/**
 * Performs initializiation/preparation operations related to
 * ICD and CPT entries to be needed by the application
 */
GeneralController.initializeIcdAndCptEntries = function () {
  console.log('initialization', 'initializeIcdAndCptEntries')

  try {
    // Query stored ICD entries
    IcdCptStorageServices.readStorageEntries(IcdCptStorageServices.OBJECT_STORE_NAME_ICD, function (entries) {
      // Check if there are no ICD entries
      if (!entries || entries.length === 0) {
        // Trigger fetching and saving of ICD and CPT entries
        GeneralController.fetchAndSaveIcdAndCptEntries()
        return
      }
      console.log('initialization', 'ICD count: ' + entries.length)

      // Query stored CPT entries
      IcdCptStorageServices.readStorageEntries(IcdCptStorageServices.OBJECT_STORE_NAME_CPT, function (entries) {
        // Check if there are no CPT entries
        if (!entries || entries.length === 0) {
          // Trigger fetching and saving of ICD and CPT entries
          GeneralController.fetchAndSaveIcdAndCptEntries()
          return
        }
        console.log('initialization', 'CPT count: ' + entries.length)
      })
    })
  } catch (ex) {
    GeneralController.fetchAndSaveIcdAndCptEntries()
  }
}

/**
 * Performs re-creation of database and object stores,
 * fetching of ICD and CPT entries needed by the application over the network,
 * and saving to persistent storage on the browser side.
 */
GeneralController.fetchAndSaveIcdAndCptEntries = function () {
  console.log('initialization', 'fetchAndSaveIcdAndCptEntries')

  IcdCptStorageServices.initializeStorage(function () {
    // Fetch ICD count
    IcdServices.count().then(res => {
      let count = res.data.count
      // count = 10 // TODO: remove

      // Fetch and save ICD entries
      IcdServices.getList(0, count, 0).then(res => {
        // console.log('Login ICD', res.data)
        IcdCptStorageServices.saveIcdEntries(res.data)
      })
    })

    // Fetch CPT count
    CptServices.count().then(res => {
      let count = res.data.count
      // count = 10 // TODO: remove

      // Fetch and save CPT entries
      CptServices.getList(0, count, 0).then(res => {
        // console.log('Login CPT', res.data)
        IcdCptStorageServices.saveCptEntries(res.data)
      })
    })
  })
}

export { GeneralController }
